  position: relative;
  width: 250px
}
.origin-image-container .origin-image-nail {
  width: 220px;
  height: 160px;
  border: 1px dashed #ccc;
  cursor: pointer;
  display: table-cell;
  text-align: center;
  vertical-align: middle
}
.origin-image-container .origin-image-nail img {
  max-width: 210px;
  max-height: 160px
}
.origin-image-container .btn-group {
  position: absolute;
  right: 0;
  top: 0
}
.zoom-image-container {
  max-width: 360px;
  max-height: 320px;
  width: 360px;
  height: 320px;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  border:  1px dashed #ccc
}
.zoom-image-container .lens {
  background: rgba(90, 90, 90, .2);
  top: 0;
  left: 0;
  border: 1px solid red;
  background: url(../assets/images/benz-02.jpg);
  background-repeat: no-repeat;
  width: 320px;
  height: 220px;
  display: inline-block
}

.rotate-circle {
  width: 8px;
  height: 8px;
  background: #ccc;
  position: relative;
  display: inline-block;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
  cursor: pointer
}

.rotate-circle:hover {
  background: #00BAA6
}

.rotate-circle:hover:before {
  border-color: #00BAA6 transparent #00BAA6 #00BAA6
}

.rotate-circle:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  border: 2px solid #ccc;
  border-radius: 50%;
  right: 13px;
  position: relative;
  top: 2px;
  z-index: 3;
  border-right-color: transparent;
  border-bottom-style: dashed;
  border-left-style: dashed
}

.rotate-circle:after {
  content: "";
  display: block;
  width: 7px;
  height: 7px;
  background: #FFF;
  position: relative;
  top: -25px;
  right: 1px
}



